// åœ¨ LeetCode å•†åº—ä¸­ï¼Œ æœ‰ n ä»¶åœ¨å”®çš„ç‰©å“ã€‚æ¯ä»¶ç‰©å“éƒ½æœ‰å¯¹åº”çš„ä»·æ ¼ã€‚ç„¶è€Œï¼Œä¹Ÿæœ‰ä¸€äº›å¤§ç¤¼åŒ…ï¼Œæ¯ä¸ªå¤§ç¤¼åŒ…ä»¥ä¼˜æƒ çš„ä»·æ ¼æ†ç»‘é”€å”®ä¸€ç»„ç‰©å“ã€‚

// ç»™ä½ ä¸€ä¸ªæ•´æ•°æ•°ç»„ price è¡¨ç¤ºç‰©å“ä»·æ ¼ï¼Œå…¶ä¸­ price[i] æ˜¯ç¬¬ i ä»¶ç‰©å“çš„ä»·æ ¼ã€‚å¦æœ‰ä¸€ä¸ªæ•´æ•°æ•°ç»„ needs è¡¨ç¤ºè´­ç‰©æ¸…å•ï¼Œå…¶ä¸­ needs[i] æ˜¯éœ€è¦è´­ä¹°ç¬¬ i ä»¶ç‰©å“çš„æ•°é‡ã€‚

// è¿˜æœ‰ä¸€ä¸ªæ•°ç»„ special è¡¨ç¤ºå¤§ç¤¼åŒ…ï¼Œspecial[i] çš„é•¿åº¦ä¸º n + 1 ï¼Œå…¶ä¸­ special[i][j] è¡¨ç¤ºç¬¬ i ä¸ªå¤§ç¤¼åŒ…ä¸­å†…å«ç¬¬ j ä»¶ç‰©å“çš„æ•°é‡ï¼Œä¸” special[i][n] ï¼ˆä¹Ÿå°±æ˜¯æ•°ç»„ä¸­çš„æœ€åŽä¸€ä¸ªæ•´æ•°ï¼‰ä¸ºç¬¬ i ä¸ªå¤§ç¤¼åŒ…çš„ä»·æ ¼ã€‚

// è¿”å›ž ç¡®åˆ‡ æ»¡è¶³è´­ç‰©æ¸…å•æ‰€éœ€èŠ±è´¹çš„æœ€ä½Žä»·æ ¼ï¼Œä½ å¯ä»¥å……åˆ†åˆ©ç”¨å¤§ç¤¼åŒ…çš„ä¼˜æƒ æ´»åŠ¨ã€‚ä½ ä¸èƒ½è´­ä¹°è¶…å‡ºè´­ç‰©æ¸…å•æŒ‡å®šæ•°é‡çš„ç‰©å“ï¼Œå³ä½¿é‚£æ ·ä¼šé™ä½Žæ•´ä½“ä»·æ ¼ã€‚ä»»æ„å¤§ç¤¼åŒ…å¯æ— é™æ¬¡è´­ä¹°ã€‚


export function shoppingOffers(price: number[], special: number[][], needs: number[]): number {
  // n: n ä»¶åœ¨å”®
  // price: price[i] æ˜¯ç¬¬ i ä»¶å•†å“çš„ä»·æ ¼
  // needsï¼šneeds[i] æ˜¯éœ€è¦è´­ä¹°ç¬¬ i ä»¶å•†å“çš„æ•°é‡
  // special:
  //  special[i]: é•¿åº¦ä¸º n + 1
  //  special[i][j]: è¡¨ç¤ºç¬¬ i ä¸ªå¤§ç¤¼åŒ…ä¸­å†…å«ç¬¬ j ä»¶ç‰©å“çš„æ•°é‡ã€‚ä¸” special[i][n] ï¼ˆä¹Ÿå°±æ˜¯æ•°ç»„ä¸­çš„æœ€åŽä¸€ä¸ªæ•´æ•°ï¼‰ä¸ºç¬¬ i ä¸ªå¤§ç¤¼åŒ…çš„ä»·æ ¼ã€‚

  // è¾“å…¥ï¼šprice = [2,5], special = [[3,0,5],[1,2,10]], needs = [3,2]
  // è¾“å‡ºï¼š14
  // è§£é‡Šï¼šæœ‰ A å’Œ B ä¸¤ç§ç‰©å“ï¼Œä»·æ ¼åˆ†åˆ«ä¸º Â¥2 å’Œ Â¥5 ã€‚ 
  // å¤§ç¤¼åŒ… 1 ï¼Œä½ å¯ä»¥ä»¥ Â¥5 çš„ä»·æ ¼è´­ä¹° 3A å’Œ 0B ã€‚ 
  // å¤§ç¤¼åŒ… 2 ï¼Œä½ å¯ä»¥ä»¥ Â¥10 çš„ä»·æ ¼è´­ä¹° 1A å’Œ 2B ã€‚ 
  // éœ€è¦è´­ä¹° 3 ä¸ª A å’Œ 2 ä¸ª B ï¼Œ æ‰€ä»¥ä»˜ Â¥10 è´­ä¹° 1A å’Œ 2Bï¼ˆå¤§ç¤¼åŒ… 2ï¼‰ï¼Œä»¥åŠ Â¥4 è´­ä¹° 2A ã€‚


  special = special.filter(gift => {
    let sum = 0
    for(let i = 0; i < gift.length - 1; i++) {
      sum += price[i] * gift[i]
    }

    return gift[gift.length - 1] < sum
  })

  function isAll(x: number[]) {
    return x.every(v => v === 0)
  }

  function isOut(x: number[]) {
    return x.some(v => v < 0)
  }
 
  let result = Number.MAX_SAFE_INTEGER

  const map = new Map<string, number>()

  function dfs(hasBuy: number[], allPrice: number) {
    const serialize = hasBuy.join('$')
    if(map.has(serialize) && map.get(serialize)! <= allPrice) return
    map.set(serialize, allPrice)
    if(allPrice >= result) return
    if(isAll(hasBuy)) {
      result = Math.min(allPrice, result)
      return
    }

    let canBuyGift = false

    special.forEach((gift) => {
      const next = [...hasBuy]
      next.forEach((_, j) => {
        next[j] -= gift[j]
      })
      if(!isOut(next)) {
        canBuyGift = true
        dfs(next, allPrice + gift[gift.length - 1])
      }
    })

    if(!canBuyGift) {
      price.forEach((_, i) => {
        const next = [...hasBuy]
        next[i] -= 1
        if(!isOut(next)) dfs(next, allPrice + price[i])
      })
    }
  }


  dfs(needs, 0)
  return result
};